import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { graphql, useStaticQuery, navigate } from "gatsby"
import PopularList from "../components/PopularList"
import Typography from "@material-ui/core/Typography"
import Box from "@material-ui/core/Box"
import makeStyles from "@material-ui/core/styles/makeStyles"
import Grid from "@material-ui/core/Grid"
import Paper from "@material-ui/core/Paper"
import Img from "gatsby-image"
import List from "@material-ui/core/List"
import ListItem from "@material-ui/core/ListItem"
import ListItemIcon from "@material-ui/core/ListItemIcon"
import ListItemText from "@material-ui/core/ListItemText"
import LocationOn from "@material-ui/icons/LocationOn"
import DraftsIcon from "@material-ui/icons/Drafts"
import PhoneIcon from "@material-ui/icons/Phone"
import Link from "@material-ui/core/Link"
import { documentToHtmlString } from "@contentful/rich-text-html-renderer"
import CategoryList from "../components/CategoryList";
import { options } from "../option";

const useStyles = makeStyles(theme => ({
  siteInfo: {
    minHeight: "calc(100vh - 48px)",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "left",
    color: theme.palette.text.secondary,
  },
  text: {
    color: theme.palette.text.primary,
  },
}))

const IndexPage = () => {
  const data = useStaticQuery(graphql`
    query {
      allContentfulProduct {
        nodes {
          contentful_id
          price
          newPrice
          spaceId
          name
          description {
            description
          }
          photo {
            fluid {
              ...GatsbyContentfulFluid_tracedSVG
            }
          }
        }
      }
      contentfulImage(title: { eq: "backgroundFirstSection" }) {
        image {
          fluid {
            ...GatsbyContentfulFluid_tracedSVG
          }
        }
      }
      contentfulRichText(title: { eq: "О магазине (текст)" }) {
        text {
          json
        }
      }
    }
  `)
  const products = data.allContentfulProduct.nodes
  const image = data.contentfulImage.image.fluid
  const shopInfo = data.contentfulRichText.text.json

  const classes = useStyles()
  return (
    <Layout>
      <SEO />
      <Typography variant="srOnly" component="h1">
        Строймаркет
      </Typography>
      <Box component="section" className={classes.siteInfo}>
        {/*<Paper className={classes.paper}>*/}
          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <Box>
                <Typography
                  variant="h4"
                  component="h2"
                  align="left"
                  color="textPrimary"
                >
                  Привет! В нашем магазине вы найдете:
                </Typography>
                {/*<Typography*/}
                {/*  variant="body1"*/}
                {/*  component="div"*/}
                {/*  align="left"*/}
                {/*  color="textPrimary"*/}
                {/*>*/}
                {/*  <p*/}
                {/*    dangerouslySetInnerHTML={{*/}
                {/*      __html: documentToHtmlString(shopInfo, options),*/}
                {/*    }}*/}
                {/*  />*/}
                {/*</Typography>*/}
                <CategoryList/>
                <Typography
                  variant="h4"
                  component="h2"
                  align="left"
                  color="textPrimary"
                >
                  Контакты
                </Typography>
              </Box>
              <Box>
                <List component="nav" aria-label="main mailbox folders">
                  <ListItem button onClick={() => navigate("map")} color="#000">
                    <ListItemIcon>
                      <LocationOn />
                    </ListItemIcon>
                    <ListItemText
                      primary="г. Жабинка, ул. Пионерская улица, 4"
                      className={classes.text}
                    />
                  </ListItem>
                  <Link underline="none" href="mailto:strmarket@gmail.com">
                    <ListItem button>
                      <ListItemIcon>
                        <DraftsIcon />
                      </ListItemIcon>
                      <ListItemText
                        primary="strmarket@gmail.com"
                        className={classes.text}
                      />
                    </ListItem>
                  </Link>
                  <Link underline="none" href="tel:+375333238249">
                    <ListItem button>
                      <ListItemIcon>
                        <PhoneIcon />
                      </ListItemIcon>
                      <ListItemText
                        className={classes.text}
                        primary={"+375333238249"}
                      />
                    </ListItem>
                  </Link>
                </List>
              </Box>
            </Grid>
            <Grid item xs={12} md={6} container alignItems="center">
              <Grid xs={12}>
                <Img fluid={image} />
              </Grid>
            </Grid>
          </Grid>
        {/*</Paper>*/}
      </Box>
      <section>
        <Typography variant="h4" component="h2" color="textPrimary">
          Хиты продаж
        </Typography>
        <PopularList products={products} />
      </section>
    </Layout>
  )
}

export default IndexPage
